import { Block, BlockChildren } from '@ali/4ever-cangjie';
import type { ParagraphProperties, ParagraphChildren } from '@ali/4ever-plugin-paragraph';
import { FoldableHeadingData } from './heading1';

export default class Heading5 extends Block<FoldableHeadingData, ParagraphChildren> {
  static TYPE = 'heading-5';

  static create<SD = FoldableHeadingData, SN extends BlockChildren = ParagraphChildren>(
    properties: Heading5 | ParagraphProperties<SD, SN> | string,
  ) {
    if (typeof properties === 'string') {
      throw new Error('`Heading5.create` does not accept string');
    }
    if (Heading5.isHeading5(properties)) {
      return properties as Block<SD, SN>;
    }
    return super.create<SD, SN>({
      ...properties,
      type: Heading5.TYPE,
    });
  }

  static isHeading5(maybeHeading5: any): maybeHeading5 is Heading5 {
    return Block.isBlock(maybeHeading5) && maybeHeading5.type === Heading5.TYPE;
  }

}
